import React from 'react';
import { Modal, DropzoneUploader } from 'library/components';

type FileUploaderProps = {
  show: boolean;
  onClose: () => void;
}
export const ModalFileUploader = ({show, onClose }: FileUploaderProps) => (
  <Modal shown={show} onClose={onClose}>
    <Modal.Header title="Upload files" />
    <Modal.Body>
      <DropzoneUploader />
    </Modal.Body>
  </Modal>
)