import React from 'react';
import {FileBrowserProps} from './types';
import { FileBox } from './file-box';

export const FileBrowser = ({ data, selectable, onSelectFile }: FileBrowserProps) => {
  return (
    <div>
      {data.files.map(f => (
        <FileBox key={f.Url} file={f} selectable={selectable} onSelectFile={onSelectFile} />
      ))}
    </div>
  )
}
