import React from 'react';
import styled from 'styled-components';
import { Col } from 'reactstrap';
import { DropzoneUploader, Button } from 'library/components';
import { useFileManager } from './use-file-manager';
import { ModalFileUploader } from './modal-file-uploader';
import { FileBrowser } from './file-browser';
import { FileBrowserMenu } from './file-browser-menu';
import { FileManagerProps, EmbeddedUploaderProps } from './types';

export const FileManagerCore = ({ selectable, onSelectFile }: FileManagerProps) => {

  const { 
    toggleShowUploader, setShowEmbeddedUploader, showModalUploader, 
    showEmbeddedUploader, browserInfo,
  } = useFileManager({ selectable });

  return (
    <>
      <Col lg={3} className="d-flex flex-column">
        <FileBrowserMenu displayUploader={toggleShowUploader(true)} />
      </Col>

      <Col lg={9}>
        {showEmbeddedUploader && (<EmbeddedUploader onCancel={() => setShowEmbeddedUploader(false)}/>)}
        {!showEmbeddedUploader && (<FileBrowser data={browserInfo} selectable={selectable} onSelectFile={onSelectFile} />)}
      </Col>
      
      {!selectable && showModalUploader && (
        <ModalFileUploader show onClose={toggleShowUploader(false)} />
      )}

    </>
  )
}

const EmbeddedUploader = ({ onCancel }: EmbeddedUploaderProps) => {
  return (
    <>
      <Actions>
        <Button onClick={onCancel} outline size='sm' color='warning' >
          <i className="fa fa-chevron-left" aria-hidden="true"></i> Back
        </Button>
      </Actions>
      <br />
      <div className="animated fadeInRight">
        <DropzoneUploader embedded />
      </div>
    </>
  )
}

const Actions = styled.div`
  /* display: flex;
  justify-content: flex-end; */
`;
