/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect } from 'react';
import {sortBy} from 'lodash';
import { fetchData, useSafeState } from 'library/common';
import { BrowseRequest } from './types';
import { config } from 'config';
import { UseFileManagerProps } from './types';

export const useFileManager = ({ selectable }: UseFileManagerProps) => {
  const [showModalUploader, setShowModalUploader] = useState(false);
  const [showEmbeddedUploader, setShowEmbeddedUploader] = useState(false);

  const [browserInfo, setBrowserInfo] = useSafeState<BrowseRequest>({
    files: [],
    folders: []
  });

  const getDisplay = (key: string) => {
    const split = key.split("/");
    return split[split.length - 1];
  }
  const browseFolder = async (folder: string) => {
    const { data } = await fetchData<BrowseRequest>({
      url: `${config.apiAssets}/browse/${folder}`
    });
    setBrowserInfo({
      ...data,
      files: sortBy(data.files.map(d => ({
        ...d,
        Display: getDisplay(d.Key),
      })), f => f.Display ),
    });
  };

  const handleShowUploader = (show: boolean) => {
    if (selectable) {
      setShowEmbeddedUploader(show)
    } else {
      setShowModalUploader(show)
    }
  }

  // onSelectFiles?: (files: IFileWithMeta[]) => void;

  useEffect(() => {
    browseFolder('public');
  }, [browseFolder])

  return {
    browserInfo,
    showModalUploader,
    showEmbeddedUploader,
    setShowEmbeddedUploader,
    toggleShowUploader: (show: boolean) => () => handleShowUploader(show),
  }
}